
.dev-hint {
  position: absolute;
  right: -60%;
  line-height: 2;
  white-space: nowrap;
  border: 5px solid red;
  left: auto;
  top: 45%;
  width: 300px;
  height: 360px;
  opacity: .6;
  padding: 10px 0 0 0;
  text-align: center;
}
button {
  background-color: grey;
  color: #fff;
  padding: 12px 20px;
  border-radius: 4px;
  cursor: pointer;
  /*悬停时显示一个手*/
  text-align: center;
  font-weight: bold;
  letter-spacing: 2px;
  border: none;
  font-size: 16px;
}
.speedControl {
  padding: 8px 8px;
}



